<template>
	<view class="flex flex-direction">
		<cu-custom bgColor="bg-cyan" :isBack="preview">
			<block slot="content">组织管理</block>
		</cu-custom>
		<!-- 当前组织 -->
		<uni-list>
			<uni-list-item
				:thumb="(current.Logo == null || current.Logo == '' || current.Logo == 'string') ? '/static/organ/organ_default.png': current.Logo"
				thumbSize="lg">
				<template slot="body" style="width: 65%;">
					<navigator url="/pages/basic/orgation/orgationdetail?preview=true">
						<view class="text-bold text-cut text-lg">{{ current.Name ?  current.Name:'' }}</view>
						<view class=" cf ">
							<view class="fl text-sm padding-top-xs">
								{{ current.Area1Name }}
								{{(current.Area1Name == null|| current.Area1Name == '') ? '' :'-'}}
								{{ current.Area2Name }}
								{{(current.Area2Name == null|| current.Area2Name == '') ? '' :'-'}}
								{{ current.Area3Name }}
							</view>
							<view class="text-sm fr cu-tag">{{ current.IndustryName }}</view>
						</view>
						<view class="text-sm text-cut text-grey">联系方式：{{ current.LinkTel ? current.LinkTel:'' }}</view>
						<view class="text-sm text-cut text-grey">地址：{{ current.Address ? current.Address :''}}</view>
					</navigator>
				</template>
				<template slot="footer" style="width: 20%;">
					<navigator class="flex  padding-xl justify-center"
						url="/pages/basic/orgation/orgation_switch?preview=true">
						<view class="cu-tag margin-lr round bg-blue light ">切换</view>
					</navigator>
				</template>
			</uni-list-item>
		</uni-list>
		<view v-for="(item , i) in tabber_list" :key="i">
			<!-- 标题 -->
			<view v-if="item.iconsList.length > 0 && item.title_show" class="cu-bar bg-white solid-top ">
				<view class="action">
					<text class="cuIcon-title " :class="'text-'+item.color"></text> {{ item.title}}
				</view>
			</view>
			<!-- 宫格 -->
			<view v-if="item.iconsList.length > 0" class="cu-list grid no-border solid-bottom "
				:class="'col-'+ item.colNumber + ' ' + item.bgColor" :data-cur="item.cur">
				<!-- 总宫格数小于等于item.colNumber*2 || 总宫格大于item.colNumber*2 ， index < item.colNumber*2 -1-->
				<view class="cu-item " v-for="(icon,index) in item.iconsList" :key="index"
					v-if="(item.iconsList.length <= item.colNumber*2) || (item.iconsList.length > item.colNumber*2 && index < item.colNumber*2 -1)"
					@tap="navigateTo" :data-url="icon.url" :data-exec="icon.exec">
					<view :class="['cuIcon-' + icon.cuIcon,'text-' + icon.color]">
					</view>
					<text>{{icon.name}}</text>
				</view>

				<!-- 总宫格大于item.colNumber*2 ，index > item.colNumber*2-1 -->
				<view class="cu-item " v-for="(icon,index) in item.iconsList" :key="index"
					v-if="item.iconsList.length > item.colNumber*2 && index >= item.colNumber*2 -1 && item.showAll"
					@tap="navigateTo" :data-url="icon.url">
					<view :class="['cuIcon-' + icon.cuIcon,'text-' + icon.color]">
					</view>
					<text>{{icon.name}}</text>
				</view>
				<!-- 全部或折叠按钮 -->
				<view v-if="item.iconsList.length >item.colNumber*2" class="cu-item" :data-cur="item.cur"
					@click="showAllGrid">
					<view class=" text-blue" :class="item.showAll ? 'cuIcon-fold' : 'cuIcon-apps'">
					</view>
					<text>{{ item.showAll ? '折叠' :'全部' }}</text>
				</view>
			</view>
		</view>
		<!-- 邀请新成员弹出框 -->
		<organ-invte :MchId="MchId" :Token="Token" :UserId="UserId" @show="inviteModalShow"></organ-invte>
	</view>
</template>

<script>
	/**
	 * 当前组织管理
	 * @author 陆彦捷
	 * @date 2021.06.16
	 * @date 2021.07.13 添加产品目录
	 * @date 2021.07.23 重新引用login
	 * @description  获取当前的组织信息
	 */
	import organ from '@/common/storage/organstorage.js'
	var _self;
	export default {
		data() {
			return {
				// 页面样式
				preview: false, // 是否跳转到上一页
				tabber_list: [{
						title: '成员管理',
						cur: 'applications',
						bgColor: 'bg-blue light', // 背景颜色
						color: 'blue',
						title_show: false,
						showAll: false,
						colNumber: 3, // 显示的列数
						iconsList: [{
								cuIcon: 'group_fill',
								color: 'blue',
								name: '组织机构',
								url: '/pages/basic/orgation/organtion'
							},
							{
								cuIcon: 'friendadd',
								color: 'blue',
								name: '新成员申请',
							},
							{
								cuIcon: 'friendaddfill',
								color: 'blue',
								name: '邀请新成员',
								exec: 'invite'
							},
						]
					},
					{
						title: '基础数据',
						cur: 'basic',
						bgColor: '', // 背景颜色
						color: 'blue',
						title_show: true,
						showAll: false,
						colNumber: 5, // 显示的列数
						iconsList: [{
								cuIcon: 'text',
								color: 'yellow',
								name: '基本信息',
								url: '/pages/basic/orgation/orgationdetail?preview=true'
							},
							{
								cuIcon: 'peoplelist',
								color: 'blue',
								name: '员工管理',
								url: '/pages/basic/staff/stafflist?preview=true'
							},
							
						]
					},{
						title: '产品目录',
						cur: 'goods',
						bgColor: '', // 背景颜色
						color: 'blue',
						title_show: true,
						showAll: false,
						colNumber: 5, // 显示的列数
						iconsList: [{
								cuIcon: 'list',
								color: 'yellow',
								name: '产品分类',
								url: '/pages/basic/product/producttype/home?preview=true'
							},
							{
								cuIcon: 'goodsnew',
								color: 'blue',
								name: '产品型号',
								url: '/pages/basic/product/productmodel/home?preview=true'
							},
							{
								cuIcon: 'shake',
								color: 'olive',
								name: '设备',
								url: '/pages/basic/product/product/home?preview=true'
							},
							{
								cuIcon: 'questionfill',
								color: 'yellow',
								name: '常见问题',
								url: '/pages/basic/product/problem/home?preview=true'
							}]
					},
					{
						title: '权限管理',
						cur: 'goods',
						bgColor: '', // 背景颜色
						color: 'blue',
						title_show: true,
						showAll: false,
						colNumber: 5, // 显示的列数
						iconsList: [{
							cuIcon: 'goodsfill',
							color: 'red',
							name: '权限设置',
							// url: '/pages/basic/permission/home'
						}]
					}
				], //  列表显示项
				showInviteF : null,			// 邀请新成员对话框
				// 系统相关数据
				Token: this.global.login.getToken(), // 当前Token
				MchId: organ.getIndustryId(), //当前组织id
				current: organ.getCurrentOrg(), // 当前组织信息
			}
		},
		onLoad: function(options) {
			_self = this;
			this.getParams(options);
			if (!this.current) {
				this.request_currenOrg();
			}
		},
		methods: {
			// ============================================== 初始化信息 ============================================
			/** 获取初始化参数
			 * @param {Object} options 参数
			 */
			getParams(options) {
				this.preview = options.preview != 'false'? Boolean(options.preview) : false;
			},
			// ============================================== 监听事件 ==============================================
			/** 跳转到新页面
			 * @param {Object} e
			 */
			navigateTo(e) {
				var url = e.currentTarget.dataset.url; // 连接的页面
				if (url != undefined) {
					// 跳转到页面
					uni.navigateTo({
						url: url
					});
				} else {
					var exec = e.currentTarget.dataset.exec;
					switch (exec) {
						case 'invite':
							this.showInviteF();
							break;
						default:
							this.default();
							break;
					}
				}
			},
			/** 展开或折叠当前宫格
			 * @param {Object} e
			 */
			showAllGrid(e) {
				var cur_grid = e.currentTarget.dataset.cur;
				// 获取当前列
				var tabber = this.tabber_list.find(function(item) {
					return item.cur == cur_grid;
				})
				if (tabber != undefined) {
					tabber.showAll = !tabber.showAll;
				}
			},
			/**
			 * 打开邀请新成员对方可函数
			 * @param {function} func 打开新成员对话框函数
			 */
			inviteModalShow : function(func){
				this.showInviteF = func;
			},
			// ========================================== 公共方法 =================================================
			/**
			 * 默认方法
			 */
			default () {
				uni.showModal({
					title: '开发中',
					content: '物初蒙稚，待养而成。开发中，敬请期待！'
				})
			},
			// ============================================== 请求事件 ==============================================
			/**
			 * 获取当前的组织信息(并写入local)
			 */
			request_currenOrg() {
				uni.showLoading();
				uni.request({
					url: _self.website + '/api/SvcMerchant/GetDefMch',
					header: {
						Authorization: 'Bearer ' + _self.Token
					},
					method: 'GET',
					success: function(result) {
						uni.hideLoading();
						console.log("请求当前组织成功=>", result);
						if (result.statusCode == 200) {
							if (result.data.ResultCode == "0") {
								organ.setIndustryId(result.data.Data.MchId);
								organ.setCurrentOrg(result.data.Data);
								_self.current = result.data.Data;
								return;
							}
						}
					},
					fail: function(erros) {
						uni.hideLoading();
						console.log("fail", erros);
					}
				})
			}

		}
	}
</script>

<style>

</style>
